@use 'variables';

.container {
    margin: 0 variables.$container_margin;
}

section {
    margin: 5vh 0;
    max-width: 60%;

    &.fullwidth {
        max-width: 100%;
    }
}

@media screen and (max-width: 1600px) {
    section {
        max-width: 75%;
    }
}

@media screen and (max-width: 1000px) {
    section {
        max-width: 100%;
    }
}